body {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

h1 {
    margin-top: 40px;
}

.app {
    width: 90%;
    margin: auto;
}

.form {
    width: 40%;
    margin: auto;
}

@media screen and (max-width: 480px) {
    .form {
        width: 80%;
        margin: auto;
    }
}

.unit {
    font-size: 18px;
}

.field {
    font-size: 15px;
    margin: 0;
}

button {
    margin-top: 20px;
    padding: 10px;
    font-size: 15px;
    border-radius: 5px;
    border: 1px solid black;
    background-color: indianred;
}